 * @FilePath: \工作项目协同开发\operation_system\src\components\orderManagement\FBM\Fbm_manual.vue
-->
<template>
  <div class="fbm_manual">
    <el-row>
      <el-col
        :xs="24"
        :sm="24"
        :md="{ span: 14, offset: 1 }"
        :lg="{ span: 4, offset: 1 }"
        :xl="{ span: 4, offset: 4 }"
      >
        <!-- <h4>手动发货</h4> -->
        <div class="select_opt">
          <el-form :model="fbm_formData" label-width="80px">
            <el-form-item label="选择仓库">
              <el-select
                @change="changeWarehouse"
                v-model="fbm_formData.warehouse"
                placeholder="请选择仓库"
              >
                <el-option
                  v-for="(item, i) in warehouses"
                  :key="i"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
              <!-- <el-input v-model="yc" readonly="readonly"></el-input> -->
            </el-form-item>
          </el-form>
        </div>
      </el-col>
    </el-row>
    <div class="number">
      <span>已上传未发货订单数量:</span>
      <span>{{ uploaded_num }}</span>
      <el-button
        type="primary"
        style="margin-left:33px;"
        @click="go_route('/InvoiceFileGeneration')"
        >发票文件生成</el-button
      >
    </div>

    <div class="card_com">
      <component :is="comName" :type="'com'"></component>
    </div>
  </div>
</template>

<script>
import YunCangCom from "@/components/orderManagement/FBM/phone/YunCangCom";
import EasyWarehouseCom from "@/components/orderManagement/FBM/phone/EasyWarehouseCom";
import ShunFengCom from "@/components/orderManagement/FBM/phone/ShunFengCom";
export default {
  data() {
    return {
      warehouses: [
        {
          label: "美国",
          value: "mfyc"
        },
        {
          label: "欧洲",
          value: "yc"
        }
        // {
        //   label: "顺丰",
        //   value: "sf"
        // }
      ],
      // yc:'易仓',
      fbm_formData: {
        warehouse: "yc"
      },
      comName: "EasyWarehouseCom",
      uploaded_num: 0
    };
  },
  components: {
    EasyWarehouseCom,
    YunCangCom,
    ShunFengCom
  },
  methods: {
    go_route(val) {
      this.$router.push({
        path: val
      });
    },
    changeWarehouse(val) {
      console.log(val);
      this.comName =
        val === "mfyc"
          ? "YunCangCom"
          : val === "yc"
          ? "EasyWarehouseCom"
          : "ShunFengCom";
    }
  },
  created() {
    let url = this._url2 + "server/order_management/upload_no_deliver_num/";
    this.$axios.get(url).then(res => {
      if (res.data.code === 200) {
        console.log(res);
        this.uploaded_num =
          res.data.data === null || res.data.data === undefined
            ? 0
            : res.data.data;
      }
    });
  }
};
</script>

<style scoped>
.card_com {
  /* border: 1px solid #e5e5e5; */
  /* padding: 30px 0; */
  /* background: rgba(255, 255, 255, 0.9); */
}
.fbm_manual {
  padding-top: 22px;
  position: relative;
}
.select_opt {
  width: 100%;
}
.number {
  position: absolute;
  top: 20px;
  left: 38%;
  color: #fff;
}
@media screen and (max-width: 768px) {
  .number {
    position: relative;
    color: #000;
    top: 0;
    left: 0;
    text-align: center;
    font-size: 16px;
  }
}
</style>
<style>
.fbm_manual .select_opt .el-form {
  /* width: 297px; */
  /* width: 100%; */
  background: #fff;
  /* padding: 8px; */
  /* margin-left: 30px; */
  border-radius: 4px;
}
.fbm_manual .el-select {
  width: 100%;
}
</style>



